// src/scripts/views/offline.js
 import { getStoriesFromIDB } from '../db.js'; // Adjust path if needed

 export async function renderOfflineStories(container) {
   container.innerHTML = `
     <section class="page offline-stories-page">
       <h2>Cerita Tersimpan (Offline)</h2>
       <div id="offlineStoriesContainer">Memuat cerita tersimpan...</div>
     </section>
   `;

   const offlineContainer = document.getElementById('offlineStoriesContainer');
   if (!offlineContainer) return;

   try {
     const stories = await getStoriesFromIDB();
     if (stories && stories.length > 0) {
       const html = stories.map(s => `
         <div class="story" data-id="${s.id}">
           <a href="#/story/${s.id}" class="story-link">
             <img src="${s.photoUrl}" alt="Foto oleh ${s.name || 'Unknown'}" loading="lazy" width="200"
                  style="view-transition-name: story-${s.id};">
           </a>
           <h4>${s.name || 'Unknown'}</h4>
           <p>${s.description || ''}</p>
           <small>${s.createdAt ? new Date(s.createdAt).toLocaleString() : 'No date'}</small>
           </div>
       `).join('');
       offlineContainer.innerHTML = html;
     } else {
       offlineContainer.innerHTML = '<p>Tidak ada cerita yang tersimpan di IndexedDB.</p>';
     }
   } catch (err) {
     console.error("Failed to load stories from IDB:", err);
     offlineContainer.innerHTML = '<p>Gagal memuat cerita tersimpan.</p>';
   }
 }