// src/scripts/index.js
import '../styles/style.css';
import { initRouter } from './router'; // Assuming router.js exports this
import 'leaflet/dist/leaflet.css';
// Remove direct push init/subscribe calls from here
// import initPushNotification from './push-notification-init.js';
// import { subscribePush } from './push-subscription';

// Define push-related functions or import them if needed elsewhere
import { requestNotificationPermission, subscribePush, unsubscribePush, getSubscriptionState } from './push-subscription';

document.addEventListener('DOMContentLoaded', async () => {
  // Do NOT automatically init or subscribe here
  // initPushNotification(); // REMOVE

  // Initialize SPA router
  initRouter();

  // Setup Navigation Drawer listeners (including new push buttons)
  setupNavigationDrawer(); // New function to handle drawer logic

  // Skip link accessibility
  const skipLink = document.querySelector('.skip-link');
  if (skipLink) { // Add null check
      skipLink.addEventListener('click', (e) => {
          e.preventDefault();
          const main = document.getElementById('app');
          if (main) {
              main.setAttribute('tabindex', '-1');
              main.focus();
          }
      });
  }

  // Check login status and update UI (e.g., show/hide push buttons)
  updateLoginStatusUI();

  // Set up view-transition for in-app navigation (if used)
  // setupViewTransitionNavigation(); // Assuming this exists elsewhere or is not needed here
});

function setupNavigationDrawer() {
    const drawerButton = document.getElementById('drawer-button');
    const navigationDrawer = document.getElementById('navigation-drawer');
    const pushSubscribeButton = document.getElementById('push-subscribe-button'); // Add this ID in index.html
    const pushUnsubscribeButton = document.getElementById('push-unsubscribe-button'); // Add this ID in index.html

    if (drawerButton && navigationDrawer) {
        drawerButton.addEventListener('click', () => {
            navigationDrawer.classList.toggle('open');
        });

        // Close drawer when clicking outside or on a link
        document.body.addEventListener('click', (event) => {
          if (!navigationDrawer.contains(event.target) && !drawerButton.contains(event.target)) {
            navigationDrawer.classList.remove('open');
          }
        });
        navigationDrawer.addEventListener('click', (event) => {
           // Close drawer if a navigation link inside it is clicked
           if (event.target.tagName === 'A' && event.target.getAttribute('href')?.startsWith('#')) {
                navigationDrawer.classList.remove('open');
           }
        });
    }

    // --- Push Button Logic ---
    if (pushSubscribeButton) {
        pushSubscribeButton.addEventListener('click', async () => {
            const token = localStorage.getItem('token');
            if (!token) {
                alert('Silakan login terlebih dahulu untuk berlangganan notifikasi.');
                return;
            }
            try {
                await requestNotificationPermission(); // Ensure permission is granted
                await subscribePush(token);
                alert('Berhasil berlangganan notifikasi!');
                updatePushSubscriptionUI(); 
            } catch (err) {
                console.error('Gagal berlangganan notifikasi:', err);
                alert(`Gagal berlangganan: ${err.message}`);
            }
            if (navigationDrawer) navigationDrawer.classList.remove('open');
        });
    }

    if (pushUnsubscribeButton) {
        pushUnsubscribeButton.addEventListener('click', async () => {
            const token = localStorage.getItem('token');
            if (!token) {
                alert('Silakan login terlebih dahulu.'); 
                return;
            }
            try {
                await unsubscribePush(token);
                alert('Berhasil berhenti berlangganan notifikasi.');
                updatePushSubscriptionUI();
            } catch (err) {
                console.error('Gagal berhenti berlangganan:', err);
                alert(`Gagal berhenti berlangganan: ${err.message}`);
            }
             if (navigationDrawer) navigationDrawer.classList.remove('open'); 
        });
    }
    updatePushSubscriptionUI();
}

async function updatePushSubscriptionUI() {
  const pushSubscribeButton = document.getElementById('push-subscribe-button');
  const pushUnsubscribeButton = document.getElementById('push-unsubscribe-button');
  const subscribeLi = document.getElementById('push-subscribe-li');
  const unsubscribeLi = document.getElementById('push-unsubscribe-li');
  const isLoggedIn = !!localStorage.getItem('token');
  console.log('updatePushSubscriptionUI called. isLoggedIn:', isLoggedIn);

  // --- ADD NULL CHECKS ---
  if (!subscribeLi || !unsubscribeLi || !pushSubscribeButton || !pushUnsubscribeButton) {
       console.warn("Push UI elements not found yet."); // Changed to warn
       // Optionally, try again after a short delay if elements might render later
       // setTimeout(updatePushSubscriptionUI, 100);
       return; // Exit if elements aren't found
  }
  // ----------------------

  // Reset buttons state (keep this)
  pushSubscribeButton.disabled = false;
  pushSubscribeButton.innerHTML = '<i class="fas fa-bell"></i> Subscribe';
  pushUnsubscribeButton.disabled = false;
  pushUnsubscribeButton.innerHTML = '<i class="fas fa-bell-slash"></i> Unsubscribe';


  if (!isLoggedIn || !('Notification' in window) || !('serviceWorker' in navigator) || !('PushManager'in window)) {
      console.log('Hiding push buttons (not supported or not logged in).');
      subscribeLi.style.display = 'none';
      unsubscribeLi.style.display = 'none';
      return;
  }

  try {
      const state = await getSubscriptionState();
      console.log("Push subscription state:", state);
       if (state.isSubscribed) {
           console.log('Showing Unsubscribe button.');
           subscribeLi.style.display = 'none';
           unsubscribeLi.style.display = 'list-item'; // Use list-item
       } else if (state.permission === 'granted') {
           console.log('Showing Subscribe button.');
           subscribeLi.style.display = 'list-item';
           unsubscribeLi.style.display = 'none';
       } else { // denied or default
           console.log('Showing Subscribe button (permission not granted/default).');
           subscribeLi.style.display = 'list-item';
           unsubscribeLi.style.display = 'none';
           if (state.permission === 'denied') {
              pushSubscribeButton.disabled = true;
              pushSubscribeButton.textContent = 'Permission Denied';
           }
       }
  } catch (error) {
     console.error("Error updating push UI:", error);
     // Fallback: Hide both on error? Or show subscribe disabled?
     subscribeLi.style.display = 'none';
     unsubscribeLi.style.display = 'none';
  }
}
function updateLoginStatusUI() {
  updatePushSubscriptionUI();

}

window.appUpdateLoginStatusUI = updateLoginStatusUI;

