// src/scripts/db.js
import { openDB } from 'idb';

const DB_NAME = 'story-app';
const DB_VERSION = 1;
const STORE_STORIES = 'stories';
const STORE_OUTBOX = 'outbox';

const dbPromise = openDB(DB_NAME, DB_VERSION, {
  upgrade(db) {
    if (!db.objectStoreNames.contains(STORE_STORIES)) {
      db.createObjectStore(STORE_STORIES, { keyPath: 'id' });
    }
    if (!db.objectStoreNames.contains(STORE_OUTBOX)) {
      db.createObjectStore(STORE_OUTBOX, { autoIncrement: true });
    }
  },
});

export async function saveStoriesToIDB(stories) {
  const db = await dbPromise;
  const tx = db.transaction(STORE_STORIES, 'readwrite');
  for (const s of stories) {
    if (s.id) {
      tx.store.put(s);
    } else {
      console.warn('Story without id skipped:', s);
    }
  }
  await tx.done;
}

export async function getStoriesFromIDB() {
  const db = await dbPromise;
  return db.getAll(STORE_STORIES);
}

export async function saveOutboxItem(item) {
  const db = await dbPromise;
  const tx = db.transaction(STORE_OUTBOX, 'readwrite');

  // --- Store components, not the FormData object ---
  const { token, formData } = item;
  const description = formData.get('description');
  const photoBlob = formData.get('photo'); // Assuming 'photo' holds the Blob
  const lat = formData.get('lat');
  const lon = formData.get('lon');
  const photoFileName = photoBlob instanceof File ? photoBlob.name : 'photo.jpg'; // Get filename if available

  const itemToStore = {
    token: token,
    description: description,
    photoBlob: photoBlob, // Store the actual Blob
    photoFileName: photoFileName, // Store the filename
    lat: lat || null, // Store null if not present
    lon: lon || null, // Store null if not present
    timestamp: new Date().toISOString() // Optional: add a timestamp
  };
  // ------------------------------------------------

  console.log("Storing in outbox:", itemToStore); // Log what's being stored
  await tx.store.add(itemToStore); // Use await here
  await tx.done;
  console.log("Item added to outbox store.");
}

export async function getOutboxItems() {
  const db = await dbPromise;
  return db.getAll(STORE_OUTBOX);
}

export async function clearOutbox() {
  const db = await dbPromise;
  const tx = db.transaction(STORE_OUTBOX, 'readwrite');
  tx.store.clear();
  await tx.done;
}

// ‚Üê NEW: Delete one story by its id from the 'stories' store
export async function deleteStoryFromIDB(id) {
  const db = await dbPromise;
  const tx = db.transaction(STORE_STORIES, 'readwrite');
  tx.store.delete(id);
  await tx.done;
}
