// src/scripts/home.js
import { initMap, addMarker } from '../map'; // Assuming ../map is correct path
import { fetchStories } from '../presenters/homePresenter';
import { saveStoriesToIDB, getStoriesFromIDB } from '../db.js';
// Remove deleteStoryFromIDB import:
// import { deleteStoryFromIDB } from '../db.js';

export async function renderHome(container) {
  const token = localStorage.getItem('token');
  if (!token) {
    location.hash = '#/login';
    return;
  }

  container.innerHTML = `
    <section class="page home-page">
      <a id="mainContent" tabindex="-1"></a>
      <h2>Semua Cerita</h2>
      <button id="logoutBtn">Logout</button> <div id="storiesContainer">Memuat cerita‚Ä¶</div>
      <div style="width:100%;">
        <div id="map" class="map-fullwidth"></div>
      </div>
    </section>
  `;

  // --- Logout Button ---
  const logoutBtn = document.getElementById('logoutBtn');
  if(logoutBtn) {
      logoutBtn.addEventListener('click', () => {
          localStorage.removeItem('token');
          // Update UI after logout
           if (window.appUpdateLoginStatusUI) {
              window.appUpdateLoginStatusUI();
          }
          location.hash = '#/login';
      });
  }


  // --- Load and Render Stories ---
  let stories = [];
  let mapInstance = null; // Hold map instance

  try {
    console.log("Fetching stories from network...");
    stories = await fetchStories(token);
    console.log(`Fetched ${stories.length} stories. Saving to IDB...`);
    await saveStoriesToIDB(stories);
    console.log("Stories saved to IDB.");
  } catch (err) {
    console.warn('Fetch failed, falling back to IDB:', err);
    try {
        stories = await getStoriesFromIDB();
        console.log(`Loaded ${stories.length} stories from IDB.`);
        if (stories.length === 0) {
            // Optional: Show a specific message if offline and IDB is empty
             document.getElementById('storiesContainer').innerHTML = '<p>Gagal memuat cerita. Coba lagi saat online atau lihat cerita offline jika tersedia.</p>';
        }
    } catch (idbError) {
       console.error("Failed to load stories from IDB:", idbError);
       document.getElementById('storiesContainer').innerHTML = '<p>Gagal memuat cerita dari sumber manapun.</p>';
       stories = []; // Ensure stories is an empty array on complete failure
    }
  }

  // Render stories to the DOM
  renderStoriesList(stories); // Renamed function

  // Initialize map and add markers
  try {
     mapInstance = initMap(); // Assuming initMap is synchronous or handles async internally
     stories.forEach(s => {
       if (s.lat && s.lon) {
         addMarker(mapInstance, s.lat, s.lon, `${s.name || 'Unknown'}: ${s.description || ''}`);
       }
     });
  } catch(mapError) {
     console.error("Failed to initialize map or add markers:", mapError);
     // Handle map initialization error if necessary
  }

} // End of renderHome


function renderStoriesList(stories) { // Renamed function
  const container = document.getElementById('storiesContainer');
  if (!container) return;

  if (!stories || stories.length === 0) {
     container.innerHTML = 'Tidak ada cerita untuk ditampilkan.';
     return;
  }

  const html = stories.map(s => `
    <div class="story" data-id="${s.id}">
      <a href="#/story/${s.id}" class="story-link">
        <img src="${s.photoUrl}" alt="Foto oleh ${s.name || 'Unknown'}" loading="lazy" width="200"
             style="view-transition-name: story-${s.id};">
      </a>
      <h4>${s.name || 'Unknown'}</h4>
      <p>${s.description || ''}</p>
      <small>${s.createdAt ? new Date(s.createdAt).toLocaleString() : 'No date'}</small>
      </div>
  `).join('');
  container.innerHTML = html;
}

