const VAPID_PUBLIC_KEY = 'BCCs2eonMI-6H2ctvFaWg-UYdDv387Vno_bzUzALpB442r2lCnsHmtrx8biyPi_E-1fSGABK_Qs_GlvPoJJqxbk';

const urlBase64ToUint8Array = (base64String) => {
  // ... (keep existing implementation)
  const padding = '='.repeat((4 - (base64String.length % 4)) % 4);
  const base64 = (base64String + padding)
    .replace(/-/g, '+')
    .replace(/_/g, '/');
  const rawData = atob(base64);
  return Uint8Array.from([...rawData].map((char) => char.charCodeAt(0)));
};

// --- Request Permission ---
export async function requestNotificationPermission() {
    if (!('Notification' in window)) {
        throw new Error('Notifications not supported by this browser.');
    }

    const permission = await Notification.requestPermission();
    if (permission !== 'granted') {
        throw new Error('Notification permission denied.');
    }
    return permission;
}


// --- Subscribe ---
export async function subscribePush(apiToken) {
  if (!('serviceWorker' in navigator) || !('PushManager' in window)) {
    throw new Error('Push Notifications not supported.');
  }
  if (!apiToken) {
      throw new Error('Authentication token is required to subscribe.');
  }

  // Ensure permission is granted before proceeding
  const permission = Notification.permission;
  if (permission !== 'granted') {
      throw new Error(`Notification permission is ${permission}. Please grant permission.`);
  }

  try {
    const registration = await navigator.serviceWorker.ready;
    let subscription = await registration.pushManager.getSubscription();

    if (subscription) {
      console.log('Already subscribed:', subscription);
      // Optional: Resend to backend if needed, or just return
      // await sendSubscriptionToServer(subscription, apiToken); // Uncomment if resending is desired
      return subscription; // Already subscribed
    }

    subscription = await registration.pushManager.subscribe({
      userVisibleOnly: true,
      applicationServerKey: urlBase64ToUint8Array(VAPID_PUBLIC_KEY),
    });

    console.log('‚úÖ New Subscription created:', subscription);
    await sendSubscriptionToServer(subscription, apiToken);
    return subscription;

  } catch (err) {
    console.error('‚ùå Failed to subscribe:', err);
    // Attempt to unsubscribe if subscription object exists but sending failed
    if (err.name === 'AbortError' || err.message.includes('subscribe')) {
        const registration = await navigator.serviceWorker.getRegistration();
        const currentSubscription = await registration?.pushManager?.getSubscription();
        if (currentSubscription) {
           await currentSubscription.unsubscribe();
           console.log("Cleaned up failed subscription attempt.");
        }
    }
    throw err; // Re-throw the error for the caller
  }
}

// --- Unsubscribe ---
export async function unsubscribePush(apiToken) {
  if (!('serviceWorker' in navigator) || !('PushManager' in window)) {
    throw new Error('Push Notifications not supported.');
  }
   if (!apiToken) {
      throw new Error('Authentication token is required to unsubscribe.');
  }

  try {
    const registration = await navigator.serviceWorker.ready;
    const subscription = await registration.pushManager.getSubscription();

    if (!subscription) {
      console.log('Not currently subscribed.');
      return true; // Nothing to unsubscribe
    }

    console.log('Current subscription:', subscription);

    // Send request to backend first to remove subscription there
    await removeSubscriptionFromServer(subscription, apiToken);

    // Then unsubscribe locally
    const unsubscribed = await subscription.unsubscribe();
    if (unsubscribed) {
      console.log('‚úÖ Successfully unsubscribed locally.');
    } else {
      console.warn('Local unsubscription call returned false.');
    }
    return unsubscribed;

  } catch (err) {
    console.error('‚ùå Failed to unsubscribe:', err);
    // Consider if local unsubscribe should still be attempted if server call fails
    throw err; // Re-throw error
  }
}

// --- Check Subscription State ---
export async function getSubscriptionState() {
    if (!('Notification' in window) || !('serviceWorker' in navigator) || !('PushManager' in window)) {
        return { supported: false, permission: 'default', isSubscribed: false };
    }

    const permission = Notification.permission;
    const registration = await navigator.serviceWorker.ready;
    const subscription = await registration.pushManager.getSubscription();

    return {
        supported: true,
        permission: permission,
        isSubscribed: !!subscription, // True if subscription object exists
    };
}


// --- Helper: Send Subscription to Server ---
async function sendSubscriptionToServer(subscription, apiToken) {
  const payload = subscription.toJSON();
  // Dicoding API might not support expirationTime
  delete payload.expirationTime;

  const response = await fetch(
    'https://story-api.dicoding.dev/v1/notifications/subscribe',
    {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${apiToken}`,
      },
      body: JSON.stringify(payload),
    }
  );

  const result = await response.json();
  console.log('üì¨ Dicoding Push API Subscribe Response:', result);

  if (!response.ok) {
    throw new Error(result.message || 'Subscription failed on server.');
  }
}

// --- Helper: Remove Subscription from Server ---
async function removeSubscriptionFromServer(subscription, apiToken) {
     const payload = subscription.toJSON();
     // Include endpoint for identification on backend
     const bodyPayload = { endpoint: payload.endpoint };

     const response = await fetch(
         'https://story-api.dicoding.dev/v1/notifications/unsubscribe', // Assuming this is the endpoint
         {
             method: 'POST', // Or DELETE, check API documentation
             headers: {
                 'Content-Type': 'application/json',
                 Authorization: `Bearer ${apiToken}`,
             },
             body: JSON.stringify(bodyPayload),
         }
     );

     const result = await response.json().catch(() => ({})); // Catch errors if response is not JSON
     console.log('üì¨ Dicoding Push API Unsubscribe Response:', result);

     if (!response.ok) {
         // Don't throw an error that prevents local unsubscribe, but log it
         console.error(`Server unsubscribe failed: ${response.status} - ${result.message || 'Unknown server error'}`);
         // Decide if this failure should prevent local unsubscribe. Usually, it shouldn't.
     }
}